
import { defineComponent, computed, onMounted } from 'vue';
import columnList from '../../components/column-item.vue';
import { useStore, mapGetters } from 'vuex';
import { GlobalDataProps } from '../../stores/type';
import useLoadMore from '../../hooks/use-load-more';
export default defineComponent({
  name: 'Home',
  components: { columnList },
  computed: {
    ...mapGetters('columns', { list: 'getColumns' })
  },

  setup() {
    const store = useStore<GlobalDataProps>();
    const total = computed(() => store.state.columns.total);
    const currentPage = computed(() => store.state.columns.currentPage);
    // 全局类型会有自动补全功能
    onMounted(() => {
      store.dispatch('columns/getColumnsAction', { pageSize: 6 });
    });
    // 状态储存是响应式，从store实例读取状态最简单的方法是在计算属性中返回某个状态

    // const list = computed(() => store.getters.getColumns);
    // const list = [{}];
    // const biggerColumnLen = computed(() => store.getters.biggerColumnLen);
    const { loadMorePage, isLastPage } = useLoadMore('columns/getColumnsAction', total, {
      pageSize: 6,
      currentPage: currentPage.value ? currentPage.value + 1 : 2
    });

    return {
      // biggerColumnLen,
      // list,
      loadMorePage,
      isLastPage
    };
  }
});
