
import { defineComponent } from 'vue';
export default defineComponent({
  props: {
    title: {
      type: String
    },
    visible: {
      type: Boolean,
      default: false
    },
    cancleText: {
      type: String,
      default: '取消'
    },
    confirmText: {
      type: String,
      default: '确定'
    }
  },
  emits: ['modal-on-close', 'modal-on-confirm'],
  setup(props, context) {
    const onClose = () => {
      context.emit('modal-on-close');
    };
    const onConfirm = () => {
      context.emit('modal-on-confirm');
    };
    return { onClose, onConfirm };
  }
});
